// @flow
import intervalDurationGetters from './intervalDurationGetters'
import { calculateTargetTime, getIntervalType } from "./common"

export default function(
  currentTimeCounter: number,
  spentTimeCounter: number,
  settings: SettingsStateType
): Array<IntervalSchemeType> {
  let scheme: Array<IntervalSchemeType> = []
  let innerSpentTimeCounter = 0

  const getCurrentIntervalWidth = intervalWidthGetter(settings)
  const currentInterval = intervalDurationGetters(
    spentTimeCounter,
    settings
  ).current()
  const currentIntervalType = getIntervalType(currentInterval, settings)

  for (let pomidoroCounter = 0; pomidoroCounter < settings.target; ) {
    const innerCurrentInterval = intervalDurationGetters(
      innerSpentTimeCounter,
      settings
    ).current()
    const innerCurrentIntervalType = getIntervalType(innerCurrentInterval, settings)
    const allSpentTime = currentInterval - currentTimeCounter + spentTimeCounter

    const isCurrentIntervalActive =
      allSpentTime > innerSpentTimeCounter &&
      allSpentTime < innerSpentTimeCounter + innerCurrentInterval
    const isPassed = allSpentTime > innerSpentTimeCounter
    const backgroundWidth = isCurrentIntervalActive ?
      getActiveIntervalBackgroundWidth(currentTimeCounter, currentIntervalType, settings)
      : isPassed ? '100%' : '0'

    scheme.push({
      interval: {
        width: getCurrentIntervalWidth(innerCurrentIntervalType),
        color: getColor(innerCurrentIntervalType, false),
      },
      background: {
        width: backgroundWidth,
        color: getColor(innerCurrentIntervalType, true),
      },
    })

    if (innerCurrentInterval === settings.durations.work * 60) pomidoroCounter++
    innerSpentTimeCounter += innerCurrentInterval
  }

  return scheme
}

function getActiveIntervalBackgroundWidth(
  currentTimeCounter: number,
  type: IntervalType,
  settings: SettingsStateType
): string {
  const intervalDuration = getIntervalDuration(type, settings)
  const width = 100 * (intervalDuration - currentTimeCounter) / intervalDuration

  return `${width}%`
}

function getIntervalDuration(type: IntervalType, settings: SettingsStateType): number {
  return settings.durations[getIntervalName(type)] * 60
}

function getIntervalName(type: IntervalType): string {
  switch (type) {
    case 'BIG_BREAK':
      return 'bigBreak'
    case 'SMALL_BREAK':
      return 'smallBreak'
    default:
      return 'work'
  }
}

function getColor(
  type: IntervalType,
  isPassed: boolean
): string {
  const work = {
    passed: 'red',
    notPassed: '#ff938e',
  }

  const rest = {
    passed: 'green',
    notPassed: '#adffb2',
  }

  if (isPassed) {
    switch (type) {
      case 'WORK':
        return work.passed
      default:
        return rest.passed
    }
  }

  switch (type) {
    case 'WORK':
      return work.notPassed
    default:
      return rest.notPassed
  }
}

function intervalWidthGetter(
  settings: SettingsStateType
): (type: IntervalType) => string {
  const targetTime = calculateTargetTime(settings)

  function getIntervalName(type: IntervalType): string {
    switch (type) {
      case 'BIG_BREAK':
        return 'bigBreak'
      case 'SMALL_BREAK':
        return 'smallBreak'
      default:
        return 'work'
    }
  }

  return (type: IntervalType) => {
    const interval = settings.durations[getIntervalName(type)]
    const width = 100 * interval / targetTime

    return `${width}%`
  }
}



