// @flow
import intervalDurationGetters from '../../utils/intervalDurationGetters'
import { notify } from "../../utils/notifications"

const ActionSorts = {
  DECREMENT: 'DECREMENT',
  SET_TIMERS: 'SET_TIMERS',
}

type ActionSortType = $Keys<typeof ActionSorts>

type ActionType = {
  type: ActionSortType,
  counter?: number,
  spentTimeCounter?: ?number,
}

let intervalId = 0

// Action Creators

export function init(): (dispatch: () => any, getState: () => GlobalStateType) => void {
  return (dispatch: () => any, getState: () => GlobalStateType) => {
    const state = getState()
    const initCounter = state.settings.durations.work * 60

    dispatch(setTimers(initCounter))
  }
}

export function start(): any {
  return (dispatch: () => any, getState: () => GlobalStateType) => {
    if (intervalId !== 0) return

    intervalId = setInterval(() => {
      const state = getState()
      const currentTimer = state.timer.counter

      if (currentTimer <= 0) return goToNextInterval(dispatch, state)

      dispatch(decrement())
    }, 1000)
  }
}

export function pause(): any {
  return (): void => {
    clearInterval(intervalId)
    intervalId = 0
  }
}

export function reset(): any {
  return (dispatch: () => void, getState: () => GlobalStateType) => {
    const state = getState()
    const idg = intervalDurationGetters(
      state.timer.spentTimeCounter,
      state.settings
    )
    const currentInterval = idg.current()

    dispatch(setTimers(currentInterval))
  }
}

export function next(): any {
  return (dispatch: () => void, getState: () => GlobalStateType) => {
    const state = getState()

    goToNextInterval(dispatch, state)
  }
}

function decrement(): ActionType {
  return {
    type: ActionSorts.DECREMENT,
  }
}

function setTimers(counter: number, spentTimeCounter: ?number): ActionType {
  return {
    type: ActionSorts.SET_TIMERS,
    counter,
    spentTimeCounter,
  }
}

function goToNextInterval(dispatch: () => void, state: GlobalStateType) {
  const idg = intervalDurationGetters(
    state.timer.spentTimeCounter,
    state.settings
  )
  const nextInterval = idg.next()
  const currentInterval = idg.current()
  const spentTimeCounter = state.timer.spentTimeCounter + currentInterval

  notify(nextInterval, state.settings)

  dispatch(setTimers(nextInterval, spentTimeCounter))
}

// Reducer

const initialState: TimerStateType = {
  counter: 25 * 60,
  spentTimeCounter: 0,
}

export default function reducer(
  state: TimerStateType = initialState,
  action: ActionType
): TimerStateType {
  switch (action.type) {
    case ActionSorts.DECREMENT:
      return Object.assign({}, state, {
        counter: state.counter - 1,
      })

    case ActionSorts.SET_TIMERS:
      return Object.assign({}, state, {
        counter: action.counter,
        spentTimeCounter: action.spentTimeCounter || state.spentTimeCounter,
      })

    default:
      return state
  }
}



