// @flow
const ActionSorts = {
  SET_WORK_DURATION: 'SET_WORK_DURATION',
  SET_SMALL_BREAK_DURATION: 'SET_SMALL_BREAK_DURATION',
  SET_BIG_BREAK_DURATION: 'SET_BIG_BREAK_DURATION',
  SET_ROUND_SIZE: 'SET_ROUND_SIZE',
  SET_TARGET: 'SET_TARGET',
}

type ActionSortType = $Keys<typeof ActionSorts>

type ActionType = {
  type: ActionSortType,
  value: number,
}

// Action Creators

export function setWorkDuration(value: number): ActionType {
  return {
    type: ActionSorts.SET_WORK_DURATION,
    value,
  }
}

export function setSmallBreakDuration(value: number): ActionType {
  return {
    type: ActionSorts.SET_SMALL_BREAK_DURATION,
    value,
  }
}

export function setBigBreakDuration(value: number): ActionType {
  return {
    type: ActionSorts.SET_BIG_BREAK_DURATION,
    value,
  }
}

export function setRoundSize(value: number): ActionType {
  return {
    type: ActionSorts.SET_ROUND_SIZE,
    value,
  }
}

export function setTarget(value: number): ActionType {
  return {
    type: ActionSorts.SET_TARGET,
    value,
  }
}

// Reducer

const initialState: SettingsStateType = {
  durations: {
    work: 25,
    smallBreak: 5,
    bigBreak: 30,
  },
  roundSize: 4,
  target: 10,
}

export default function reducer(
  state: SettingsStateType = initialState,
  action: ActionType
): SettingsStateType {
  switch (action.type) {
    case ActionSorts.SET_WORK_DURATION:
      return Object.assign({}, state, {
        durations: Object.assign({}, state.durations, {
          work: action.value,
        }),
      })

    case ActionSorts.SET_SMALL_BREAK_DURATION:
      return Object.assign({}, state, {
        durations: Object.assign({}, state.durations, {
          smallBreak: action.value,
        }),
      })

    case ActionSorts.SET_BIG_BREAK_DURATION:
      return Object.assign({}, state, {
        durations: Object.assign({}, state.durations, {
          bigBreak: action.value,
        }),
      })

    case ActionSorts.SET_ROUND_SIZE:
      return Object.assign({}, state, {
        roundSize: action.value,
      })

    case ActionSorts.SET_TARGET:
      return Object.assign({}, state, {
        target: action.value,
      })

    default:
      return state
  }
}



