// @flow

export function calculateTargetTime(settings: SettingsStateType): number {
  let bigBreakNumbers = Math.floor(settings.target / settings.roundSize)

  if (settings.target % settings.roundSize === 0) {
    bigBreakNumbers--
  }

  return (
    bigBreakNumbers * settings.durations.bigBreak +
    settings.target * settings.durations.work +
    (settings.target - 1 - bigBreakNumbers) * settings.durations.smallBreak
  )
}

export const getMinutes = (time: number): number => Math.floor(time / 60)

export function getIntervalType(
  interval: number,
  settings: SettingsStateType
): IntervalType {
  switch (interval) {
    case settings.durations.smallBreak * 60:
      return 'SMALL_BREAK'
    case settings.durations.bigBreak * 60:
      return 'BIG_BREAK'
    default:
      return 'WORK'
  }
}



