// @flow
import { getIntervalType } from './common'

export function getPermission(): void {
  if (!('Notification' in window))
    return alert('Ваш браузер не поддерживает уведомления.')

  const { Notification } = window

  if (Notification.permission === 'granted') return

  if (window.Notification.permission !== 'denied')
    window.Notification.requestPermission()
}

export function notify(
  nextInterval: number,
  settings: SettingsStateType
): void {
  if (!('Notification' in window))
    return alert('Ваш браузер не поддерживает уведомления.')

  const { Notification } = window

  if (Notification.permission !== 'granted')
    return alert('Нужно разрешить уведомления в браузере!')

  const intervalType = getIntervalType(nextInterval, settings)

  new Notification(getMessage(intervalType))
}

function getMessage(intervalType: IntervalType): string {
  switch (intervalType) {
    case 'SMALL_BREAK': return 'Маленький перерыв'
    case 'BIG_BREAK': return 'Большой перерыв'
    default: return 'Пора поработать'
  }
}



