// @flow
import { getMinutes } from '../utils/common'

export default function(
  timeSpentCounter: number,
  settings: SettingsStateType
): { current: () => number, next: () => number } {
  const allSpentMinutes = getMinutes(timeSpentCounter)

  function getNextRound(currentRound: number, currentInterval: number): number {
    if (currentInterval !== settings.durations.work) return currentRound

    return currentRound < settings.roundSize ? currentRound + 1 : 1
  }

  function getNextInterval(currentInterval, currentRound): number {
    const { work, smallBreak, bigBreak } = settings.durations

    if (currentInterval === smallBreak || currentInterval === bigBreak)
      return work

    if (currentRound < settings.roundSize) return smallBreak

    return bigBreak
  }

  function getInterval(
    isRequiredCurrentInterval: boolean,
    currentInterval: number,
    currentPosition: number = 0,
    currentRound: number = 1
  ): number {
    const newPosition = currentPosition + currentInterval
    const nextInterval = getNextInterval(currentInterval, currentRound)
    const nextRound = getNextRound(currentRound, currentInterval)

    if (newPosition > allSpentMinutes)
      return isRequiredCurrentInterval ? currentInterval : nextInterval

    return getInterval(
      isRequiredCurrentInterval,
      nextInterval,
      newPosition,
      nextRound
    )
  }

  return {
    current: (): number => getInterval(true, settings.durations.work) * 60,
    next: (): number => getInterval(false, settings.durations.work) * 60,
  }
}



